.hero {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  padding: 2rem;
}

.hero-content {
  text-align: center;
  max-width: 800px;
  z-index: 1;
}

.hero h1 {
  font-size: 3.5rem;
  color: #333;
  margin-bottom: 1rem;
  font-weight: 700;
}

.hero .coming-soon {
  font-size: 2rem;
  color: #666;
  margin-bottom: 1.5rem;
  font-weight: 300;
}

.hero .tagline {
  font-size: 1.5rem;
  color: #555;
  margin-bottom: 2rem;
  font-weight: 400;
}

.cta-button {
  padding: 1rem 2.5rem;
  font-size: 1.2rem;
  background-color: #0066cc;
  color: white;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
  box-shadow: 0 4px 15px rgba(0, 102, 204, 0.2);
}

.cta-button:hover {
  background-color: #0052a3;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 102, 204, 0.3);
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('../assets/background-pattern.svg');
  opacity: 0.1;
  z-index: 0;
}

@media (max-width: 768px) {
  .hero h1 {
    font-size: 2.5rem;
  }

  .hero .coming-soon {
    font-size: 1.5rem;
  }

  .hero .tagline {
    font-size: 1.2rem;
  }

  .cta-button {
    padding: 0.8rem 2rem;
    font-size: 1rem;
  }
} 