.footer {
  background-color: #f8f9fa;
  padding: 2rem;
  border-top: 1px solid #dee2e6;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.footer-links {
  margin-bottom: 1.5rem;
}

.footer-links a {
  color: #555;
  text-decoration: none;
  margin: 0 1rem;
  font-size: 0.9rem;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #0066cc;
}

.footer-legal {
  color: #777;
  font-size: 0.8rem;
}

@media (max-width: 768px) {
  .footer {
    padding: 1.5rem;
  }

  .footer-links a {
    margin: 0 0.5rem;
    font-size: 0.8rem;
  }
} 