.product {
  padding: 6rem 2rem;
  background-color: #fff;
}

.product-container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.product h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.product-intro {
  max-width: 800px;
  margin: 0 auto 3rem;
  font-size: 1.2rem;
  color: #666;
  line-height: 1.6;
}

.product-visual {
  margin: 3rem 0;
}

.product-image-placeholder {
  width: 100%;
  max-width: 600px;
  height: 400px;
  margin: 0 auto;
  background-color: #f8f9fa;
  border: 2px dashed #dee2e6;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.product-image-placeholder span {
  color: #adb5bd;
  font-size: 1.2rem;
}

.product-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin: 3rem 0;
}

.product-option {
  padding: 2rem;
  background-color: #f8f9fa;
  border-radius: 15px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-option:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.product-option h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.product-option .quantity {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 0.5rem;
}

.product-option .price {
  font-size: 1.8rem;
  color: #0066cc;
  font-weight: 600;
  margin-bottom: 1rem;
}

.product-option .description {
  color: #777;
  font-size: 0.9rem;
}

.shop-button {
  padding: 1rem 2.5rem;
  font-size: 1.2rem;
  background-color: #0066cc;
  color: white;
  border: none;
  border-radius: 30px;
  cursor: not-allowed;
  opacity: 0.7;
  transition: all 0.3s ease;
  font-weight: 500;
}

.shop-button:disabled {
  background-color: #adb5bd;
}

@media (max-width: 768px) {
  .product {
    padding: 4rem 1rem;
  }

  .product h2 {
    font-size: 2rem;
  }

  .product-intro {
    font-size: 1rem;
  }

  .product-image-placeholder {
    height: 300px;
  }

  .product-options {
    grid-template-columns: 1fr;
  }
} 